.notyf__icon--error,
.notyf__icon--success,
.notyf__icon--info {
    height: 21px;
    width: 21px;
    background: #fff;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
    position: relative;
}

// Variant: error
.notyf__icon--error::after,
.notyf__icon--error::before {
    content: "";
    background: currentColor;
    display: block;
    position: absolute;
    width: 3px;
    border-radius: 3px;
    left: 9px;
    height: 12px;
    top: 5px;
}

.notyf__icon--error::after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}

.notyf__icon--error::before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

// Variant: success
.notyf__icon--success::after,
.notyf__icon--success::before {
    content: "";
    background: currentColor;
    display: block;
    position: absolute;
    width: 3px;
    border-radius: 3px;
}

.notyf__icon--success::after {
    height: 6px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    top: 9px;
    left: 6px;
}

.notyf__icon--success::before {
    height: 11px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 5px;
    left: 10px;
}

// Variant: info
.notyf__icon--info::after,
.notyf__icon--info::before {
    content: "";
    background: currentColor;
    display: block;
    position: absolute;
    width: 3px;
    border-radius: 3px;
}

.notyf__icon--info::after {
    height: 6px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    top: 9px;
    left: 6px;
}

.notyf__icon--info::before {
    height: 11px;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 5px;
    left: 10px;
}

.notyf__toast {
    display: block;
    overflow: hidden;
    bottom: -100%;
    -webkit-animation: fadeinup 0.3s ease-in forwards;
    animation: fadeinup 0.3s ease-in forwards;
    box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.25);
    position: relative;
    padding: 0 13px;
    border-radius: 2px;
    margin-top: 12px;
    max-width: 300px;
    -webkit-transform: translateY(25%);
    transform: translateY(25%);
}

.notyf__toast--disappear {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation: fadeoutdown 0.3s forwards;
    animation: fadeoutdown 0.3s forwards;
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
}

.notyf__toast--disappear .notyf__icon,
.notyf__toast--disappear .notyf__message {
    -webkit-animation: fadeoutdown 0.3s forwards;
    animation: fadeoutdown 0.3s forwards;
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
}

.notyf__toast--disappear .notyf__message {
    -webkit-animation-delay: 0.05s;
    animation-delay: 0.05s;
}

.notyf__ripple {
    height: 400px;
    width: 400px;
    position: absolute;
    -webkit-transform-origin: bottom right;
    transform-origin: bottom right;
    right: 0;
    top: 0;
    border-radius: 50%;
    -webkit-transform: scale(0) translateY(-51%) translateX(13%);
    transform: scale(0) translateY(-51%) translateX(13%);
    z-index: 5;
    -webkit-animation: ripple 0.4s ease-out forwards;
    animation: ripple 0.4s ease-out forwards;
}

.notyf__wrapper {
    display: flex;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 15px;
    border-radius: 3px;
    position: relative;
    z-index: 10;
}

.notyf__icon {
    width: 22px;
    text-align: center;
    font-size: 1.3em;
    opacity: 0;
    -webkit-animation: fadeinup 0.3s forwards;
    animation: fadeinup 0.3s forwards;
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
    margin-right: 13px;
}

.notyf__message {
    vertical-align: middle;
    position: relative;
    opacity: 0;
    -webkit-animation: fadeinup 0.3s forwards;
    animation: fadeinup 0.3s forwards;
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
}

.notyf {
    position: fixed;
    bottom: 20px;
    right: 30px;
    color: #fff;
    z-index: 9999;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

@media only screen and (max-width: $breakpoint-sm) {
    .notyf {
        bottom: 0;
        left: 0;
        right: 0;
        align-items: normal;
    }

    .notyf__ripple {
        height: 600px;
        width: 600px;
        -webkit-animation-duration: 0.5s;
        animation-duration: 0.5s;
    }

    .notyf__toast {
        max-width: none;
        border-radius: 0;
        box-shadow: 0 -2px 7px 0 rgba(0, 0, 0, 0.13);
    }
}

//
// Keyframes for animation
//
@keyframes fadeinup {
    0% {
        opacity: 0;
        -webkit-transform: translateY(25%);
        transform: translateY(25%);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
}

@keyframes fadeoutdown {
    0% {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(25%);
        transform: translateY(25%);
    }
}

@keyframes ripple {
    0% {
        -webkit-transform: scale(0) translateY(-45%) translateX(13%);
        transform: scale(0) translateY(-45%) translateX(13%);
    }

    to {
        -webkit-transform: scale(1) translateY(-45%) translateX(13%);
        transform: scale(1) translateY(-45%) translateX(13%);
    }
}
